.icon:before {
  font-weight: normal;
  font-style: normal;
  text-align: center;
  width: 16px;
  line-height: 1;
  position: relative;
  display: inline-block;
  -webkit-font-smoothing: antialiased;
}
.header > .icon-repo:before {
  top: 1px;
  text-align: left;
}
.header > .icon-file-directory:before {
  top: 1px;
}
.tab > .icon:before {
  width: auto;
}
.theme-one-dark-ui .tab-bar .tab > .title.icon:before,
.theme-one-light-ui .tab-bar .tab > .title.icon:before {
  font-size: 1.1em;
  margin-right: 0.3em;
  width: 1em;
  top: auto;
}
.thicken {
  -webkit-font-smoothing: subpixel-antialiased;
}
.sharpen {
  text-shadow: 0 0 0;
}
